"use client";
import React, { useRef, useState, useEffect } from "react";

interface CarouselProps {
  items: React.ReactNode[];
}

export const Carousel: React.FC<CarouselProps> = ({ items }) => {
  const carouselRef = useRef<HTMLDivElement>(null);
  const [isScrollable, setIsScrollable] = useState(false);

  const checkScrollability = () => {
    if (carouselRef.current) {
      setIsScrollable(carouselRef.current.scrollWidth > carouselRef.current.clientWidth);
    }
  };

  useEffect(() => {
    checkScrollability();
    window.addEventListener("resize", checkScrollability);
    return () => window.removeEventListener("resize", checkScrollability);
  }, []);

  return (
    <div className="relative">
      <div
        ref={carouselRef}
        className="flex w-full overflow-x-scroll scroll-smooth py-10 space-x-6 no-scrollbar"
      >
        {items.map((item, index) => (
          <div key={index} className="flex-shrink-0 w-full md:w-[600px]">
            {item}
          </div>
        ))}
      </div>
    </div>
  );
};

interface CardProps {
  card: {
    category: string;
    title: string;
    src: string;
    content: React.ReactNode;
  };
  index: number;
}

export const Card: React.FC<CardProps> = ({ card }) => {
  return (
    <div className="bg-neutral-900 text-white rounded-3xl shadow-xl overflow-hidden">
      <img
        src={card.src}
        alt={card.title}
        className="w-full h-64 object-cover"
      />
      <div className="p-6">
        <h3 className="text-xl font-bold mb-1">{card.title}</h3>
        <p className="text-sm text-neutral-400 mb-4">{card.category}</p>
        <div>{card.content}</div>
      </div>
    </div>
  );
};
